.videocomponent{
  margin: auto;
    position: absolute;
    z-index: -1;
    top: 50%;
    left: 0%;
    transform: translate(0%, -50%);
    visibility: visible;
    opacity: 1;
    width: 100%;
    height: 751.969px;
    object-fit: cover;
}
