.card_home{
  background-color: #fff;
  opacity: 1;
  position: relative;
  z-index: 1;
  display: flex;
  -webkit-box-pack:'justify';
  justify-content: space-between;
  -webkit-box-align: center;
  align-items: center;
  height: 100%;
  transition: all cubic-bezier(0,.95,1,.66);

}
.card_header{
