}
.btn_features{
  border: 1px solid #f2effc;
  border-radius: 30px;
  font-size: 15px;
  padding: 12px 22px;
  margin-bottom: 25px;
  transition: .5s;
  box-shadow: 0 2px 4px #0c002e0a;
}
.btn_features:hover{
  color: #fff;
  background-color: #666666;
}
.avatarcontent{
  color: #033e8c;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 120px;
  height: 120px;
  margin-left: auto;
  margin-right: auto;
  font-weight: 400;
  transition: 450ms ease-in-out;
  border-radius: 50%;
  border: 2px solid;
}
avatarcontent::before{
  top: 50%;
  left: 50%;
  right: 50%;
  bottom: 50%;
  opacity: 0;
  background-color: #033e8c;
  position: absolute;
  content: "";
  display: inline-block;
  border-radius: 50%;
  transition: 450ms ease-in-out;
}
.avatarimg{
  font-size: 48px;
}
.avatarcontent:hover{
  background-color: #033e8c;
  color: #fff;
}
.css-3zukih{
  display: block !important;
}
video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 600px;
  -o-object-fit: cover;
     object-fit: cover;
  -o-object-position: center;
     object-position: center;
}

